import React, { useState } from "react";
import styled from "styled-components";
import Layout from "../components/Layout";
import gql from "graphql-tag";
import { Mutation } from "react-apollo";

const USER_SIGNUP = gql`
  mutation {
    signupUser(email: "huh", password: "ayy") {
      name
    }
  }
`;

const Signup: React.FunctionComponent = () => {
  const [emailInput, setEmailInput] = useState("");
  const [passwordInput, setPasswordInput] = useState("");

  return (
    <Layout title="Resource | Sign Up">
      <Mutation mutation={USER_SIGNUP}>
        {(signupUser, { loading, error, data }) => (
          <Form
            onSubmit={e => {
              e.preventDefault();
              signupUser({
                variables: { email: emailInput, password: passwordInput }
              });
              setEmailInput("");
              setPasswordInput("");
            }}
          >
            <Label>Email</Label>
            <Input onChange={e => setEmailInput(e.target.value)} type="text" />
            <Label>Password</Label>
            <Input
              onChange={e => setPasswordInput(e.target.value)}
              type="password"
            />
            <Button type="submit">SIGN UP</Button>
          </Form>
        )}
      </Mutation>
    </Layout>
  );
};

export default Signup;

const Form = styled.form`
  display: flex;
  flex-direction: column;
`;

const Label = styled.p``;

const Input = styled.input``;

const Button = styled.button`
  margin-top: 20px;
`;
